import React, { useState, useEffect } from "react";
import Header from "../../components/header/header";
import axios from "axios";
import Loading from "../../components/loading";
import ErrorMessage from "../../components/errorMessage";
import "./loginPage.css";
import { useNavigate } from "react-router-dom";

export default function Login() {
  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (localStorage.getItem("userInfo")) {
      navigate("/user");
    }
  });

  const submitHandler = async (e) => {
    e.preventDefault();

    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      setLoading(true);
      const { data } = await axios.post(
        "/users/login",
        {
          email,
          password,
        },
        config
      );
      localStorage.setItem("userInfo", JSON.stringify(data));
      setError(false);
      setLoading(false);
      navigate("/user");
    } catch (error) {
      setError(true);
      setLoading(false);
    }
  };

  return (
    <div className="login-body">
      <Header />
      {error && <ErrorMessage variant="danger"> Error </ErrorMessage>}
      {loading && <Loading />}
      <div className="container h-100">
        <div className="row login-form-area">
          <div className="col-xl-6">
            <div className="login-form-input-content">
              <div className="login-card mb-0">
                <div className="login-card-body">
                  <h4 className="text-center">Login</h4>
                  <form className="login-input" onSubmit={submitHandler}>
                    <div className="login-form-group">
                      <input
                        type="email"
                        className="login-form-control"
                        placeholder="Email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                      />
                    </div>
                    <div className="login-form-group">
                      <input
                        type="password"
                        className="login-form-control"
                        placeholder="Password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                      />
                    </div>
                    <button
                      className="btn login-form-btn submit w-100"
                      type="submit"
                    >
                      Login
                    </button>
                    {/* <div className="row social-login-btn">
											<div className="col-md-6">
												<a
													className="btn google-btn"
													href={"/googleauth"}
													style={{ textTransform: "none" }}
													role="button"
												>
													<img
														style={{
															marginBottom: "3px",
															marginRight: "23px",
															width: "20px",
														}}
														alt={"Google sign-in"}
														src={
															"https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Google_%22G%22_Logo.svg/512px-Google_%22G%22_Logo.svg.png"
														}
													/>
													Login with Google
												</a>
											</div>
											<div className="col-md-6 social-btn2">
												<a
													className="btn facebook-btn"
													href={"/facebookauth"}
													style={{ textTransform: "none" }}
													role="button"
												>
													<img
														style={{
															marginBottom: "3px",
															marginRight: "5px",
															width: "25px",
															borderRadius: "50%",
														}}
														alt={"Facebook sign-in"}
														src={
															"data:image/png;base64,"
														}
													/>
													Login with Facebook
												</a>
											</div>
										</div> */}
                  </form>
                  <p className="login-form-footer1">
                    <a href="/login" className="text-primary">
                      Forgot Password?
                    </a>
                  </p>
                  <p className="login-form-footer">
                    Dont have account?{" "}
                    <a href="/signup" className="text-primary">
                      Sign Up
                    </a>{" "}
                    now
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
